.todo-list {
  text-align: center;
  list-style: none;

  &__item {
    margin: 0 auto;
    width: 400px;
    margin-bottom: 15px;
    border: 1px solid black;
    border-radius: 5%;

    transition: transform 0.3s;

    &--completed {
      background-color: greenyellow;
    }

    &:hover {
      transform: scale(1.1);
    }
  }
}
